body.dark  {
  background-color: var(--color__gray--dark);
  color: var(--color__gray--light);
  font-size: 16px;
  font-family: var(--typography__default);
  font-weight: 400;
  display: flex;
  flex-direction: column;
  max-width: 700px;
  margin: 8px auto;

  a {
    color: var(--color__gray--light)
  }
  a:hover {
    background-color: var(--color__gray--light);
    color: var(--color__gray--dark);
  }
  p {
    color: var(--color__gray--light);
  }
  blockquote {
    border-left: 5px solid var(--color__gray--light);
    color: var(--color__gray--light)
  }
  code, pre {
    color: var(--color__gray--dark);
    font-family: monospace;
    letter-spacing: 1px;
    font-size: 0.9rem;
    padding: 2.5px;
    overflow-x: auto;
  }
  .highlight {
  position: relative;
  border-radius: 5px;

  * {
    font-family: monospace, monospace;
    background-color: unset;
    padding: unset;
    letter-spacing: unset;
  }

  & > pre {
    padding: 8px 12px;

    & > code::before {
      content: attr(data-lang);
      position: absolute;
      right: 20px;
      top: -10px;
      padding: 0 7.5px;
      border: 1px solid #828282;
      border-radius: 5px;
      text-align: center;
      color: #8fbc8f;
      background-color: #1a1a1a;
    }

    .lineno {
      color: #608079;
      padding-right: 5px;
      margin-right: 10px;
      border-right: 1px solid #656565;
    }
  }
}
  .language-plaintext > .highlight {
    background-color: #eee;
  }
  .tags {
    background-color: var(--color__gray--light);
    color: var(--color__gray--dark);
  }
  li {
    color: var(--color__gray--light)
  }
  figcaption {
    color: var(--color__gray--light)
  }
  hr.stylish {
    background-image: linear-gradient(to right, rgba(white, 0.90), rgba(white, 0.45), rgba(white, 0));
  }
  input {
    background-color: var(--color__gray--dark);
    color: var(--color__gray--light)
  }
  input::placeholder {
    color: var(--color__gray--light)
  }
  ._progress-wrapper {
    background-color: rgb(51, 51, 51);
  }

  #_progress {
    background: linear-gradient(to right, rgb(204, 204, 204) var(--scroll), transparent 0);
  }
}
